<template>
  <div id="dashboard">
    <h1 class="fw-bold title-page">Danh sách công ty</h1>

    <div class="row mb-2">
      <div class="col-12">
        <div class="input-group">
          <input
            type="text"
            placeholder="Nhập mã số thuế hoặc tên doanh nghiệp để tìm kiếm...."
            class="form-control"
            v-model="keyword"
            @keyup.enter="search()"
          />
          <button
            type="button"
            id="button-addon2"
            class="btn btn-primary-soft"
            @click="search()"
          >
            <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
          </button>

          <router-link :to="{ name: 'company-create' }" class="btn btn-primary ms-2">
            <i class="fa-duotone fa-circle-plus"></i> Tạo mới
          </router-link>
        </div>
      </div>
    </div>
    <Company :companies="companies" />

    <pagination
      v-bind:pagination="pagination"
      v-on:click.native="getAll(pagination.current_page)"
      :offset="4"
    ></pagination>
  </div>
</template>

<script>
import Company from "../../components/companies/Index.vue";
import pagination from "../../extends/Paginate";
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    Company,
    pagination,
  },
  data() {
    return {
      // companies: [],
      keyword: "",
      debounceTimeout: null,
    };
  },
  computed: {
    ...mapGetters("companies", ["companies", "pagination"]),
  },
  created() {
    this.getAll(this.pagination.current_page);
  },
  methods: {
    ...mapActions("companies", ["getCompanies"]),
    search() {
      this.getAll(this.pagination.current_page);
    },
    getAll(page) {
      this.$econtract.showLoading();
      clearTimeout(this.debounceTimeout); // Xóa timeout cũ khi người dùng tiếp tục nhập liệu
      if (typeof this.keyword == undefined) page = 1;
      this.debounceTimeout = setTimeout(() => {
        this.getCompanies({
          pageSize: 20,
          page: page,
          name: this.keyword,
        }).then(() => {
          this.$econtract.hideLoading();
        });
      }, 300);
    },
  },
};
</script>
